body {
  background-color: #f8f8f8;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

li {
  list-style: none;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  padding: 5rem 0;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-around;
    gap: 2rem;
  }
}

.navegacion {
  background-color: #8ed3cc;
  padding: 0.5rem 0;
}
.navegacion__border {
  border-top: 1px dashed #FFFFFF;
  border-bottom: 1px dashed #FFFFFF;
}
@media (min-width: 768px) {
  .navegacion__contenedor {
    display: flex;
    justify-content: space-around;
  }
}
.navegacion__enlace {
  display: block;
  color: #FFFFFF;
  text-align: center;
  margin: 1rem 0;
}

.rrss {
  margin: 5rem 0;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}
.rrss__enlace {
  width: 4rem;
}

.slider {
  padding: 3rem;
}

.bienvenida {
  text-align: center;
}
.bienvenida__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.7;
  text-transform: lowercase;
  font-weight: 400;
  font-size: 4rem;
}
.bienvenida__heading span {
  display: block;
  color: #000;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
}
.bienvenida__texto {
  font-size: 2.6rem;
  color: #979797;
  max-width: 50rem;
  margin: 4rem auto 0 auto;
  line-height: 1.8;
}

.cards {
  padding: 5rem 0;
}
.cards__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .cards__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  position: relative;
}
.card__contenido {
  background-color: #FFFFFF;
  position: absolute;
  width: auto;
  height: auto;
  margin-top: -10rem;
}